import axios from "axios";

export const sendDataApi = async (values) => {
    try {
        console.log(values)
        const response = await axios.post(`https://mkcx62ahbytdtitiiyyz7rqhai0qardz.lambda-url.us-west-2.on.aws/`, values,
            {
                headers: {
                  'Content-Type': 'application/json'
                }
            }
        );

        return response;
    } catch (error) {
        throw error; 
    }

}